<template>
  <div>
    <v-dialog v-model="dialog" persistent max-width="380">
      <v-card>
        <v-card-title class="subtitle-1"></v-card-title>

        <v-card-text class="body-1" style="color:#697f8a; font-size: 14.4px; text-align:center;">
          {{
          this.$store.state.dialogMessage
          }}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="toggleDialogBtn()">닫기</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  data: () => ({}),
  computed: {
    dialog: {
      get() {
        return this.$store.state.dialog;
      },
      set() {}
    }
  },
  props: {},
  methods: {
    toggleDialogBtn: function() {
      this.$store.state.dialog = !this.$store.state.dialog;
    }
  }
};
</script>

