<template>
  <div>
    <v-card tile class="elevation-0">
      <!-- 상단통합검색 -->
      <!-- 내용영역 -->
      <div class="pa-8 area-content">
        <!-- 상단통합검색 -->

        <!-- //상단통합검색 -->

        <!-- 내용 -->
        <section class="box-area-content">
          <!-- 테이블 -->
          <div class="content-tit">
            <h2>부서관리</h2>
          </div>
          <div id class="st-table06 table01">
            <v-row class="ma-0 pa-0">
              <v-col class="ma-0 pa-0">
                <v-sheet elevation="0" class="pa-0 ma-0">
                  <!-- <v-treeview
                    v-model="selection"
                    :items="dptList"
                    :search="searchDept"
                    return-object
                    open-all
                    class="st-department"
                  >
                    <template slot="label" slot-scope="{ item }">
                      <div class="box-department">
                        <span>{{ item.name }}</span>
                        <v-btn small class="ml-2 btn-st04 st-vtnode" @click="editItem(item)">하위 추가 +</v-btn>
                        <v-divider vertical></v-divider>
                        <v-btn small class="ml-2 btn-st04" @click="editItem(item)">수정</v-btn>
                        <v-btn small class="ml-2 btn-st04" @click="editItem(item)">삭제</v-btn>
                      </div>
                    </template>
                  </v-treeview>-->

                  <Tree
                    :data="items"
                    :indent="80"
                    :draggable="draggable"
                    :droppable="droppable"
                    @drag="drop"
                    @change="change"
                    cross-tree
                    class="st-department"
                  >
                    <div slot-scope="{ data, store }">
                      <b
                        v-if="data.children && data.children.length"
                        @click="store.toggleOpen(data)"
                      >{{ data.open ? '-' : '+' }}&nbsp;</b>
                      <input type="text" v-if="editable" v-model="data.name" />
                      <span v-else>{{ data.name }}</span>
                      <v-btn small class="ml-2 btn-st04 st-vtnode" @click="addItem(data)">하위 추가 +</v-btn>
                      <v-btn
                        small
                        v-if="!editable && data.id === 2"
                        class="ml-2 btn-st04 st-vtnode"
                        @click="editItem(data)"
                      >수정</v-btn>
                      <v-btn
                        v-if="editable && data.id === 2"
                        small
                        class="ml-2 btn-st04 st-vtnode"
                        @click="saveItem(item)"
                      >저장</v-btn>
                      <v-btn
                        v-if="editable && data.id === 2"
                        small
                        class="ml-2 btn-st04 st-vtnode"
                        @click="cancelItem(data)"
                      >취소</v-btn>
                      <v-btn v-if="data.id !== 1" small class="ml-2 btn-st04 st-vtnode">삭제</v-btn>
                    </div>
                  </Tree>
                </v-sheet>
              </v-col>
              <!-- <v-divider vertical></v-divider>
              <v-col class="pa-0" cols="6">
                <template v-if="!selection.length">
                  <v-sheet elevation="0" class="pa-12 mx-2">선택된 부서가 없습니다.</v-sheet>
                </template>
                <template v-else>
                  <div v-for="(node, index) in selection" :key="node.id">
                    <v-sheet elevation="0" class="pa-12 mx-2 mb-2">
                      <v-text-field v-model="node.id"></v-text-field>
                      <v-text-field v-model="node.bizNo"></v-text-field>
                      <v-text-field v-model="node.name"></v-text-field>
                      <v-btn small class="mr-1 mb-1" color="success" @click="saveSelect(node)">추가</v-btn>
                      <v-btn small class="mr-1 mb-1" color="error" @click="deleteSelect(node)">삭제</v-btn>
                    </v-sheet>
                  </div>
                </template>
              </v-col>-->
            </v-row>
          </div>
          <!-- // 휴게시간관리 테이블 -->
        </section>
        <!-- //내용 -->
      </div>
    </v-card>
  </div>
</template>

<script>
import { DraggableTree } from "vue-draggable-nested-tree";
export default {
  name: "",
  components: { Tree: DraggableTree },
  data: () => ({
    dptNm: "",
    draggable: false,
    droppable: false,
    editable: false,
    tree2data: [
      { text: "node 1" },
      { text: "node 2" },
      { text: "node 3" },
      { text: "node 4" }
    ],
    searchDept: "",
    selectionType: "independent",
    selection: [],
    selectDept: {},
    items: [
      {
        id: 1,
        name: "부서 생성 중",
        children: []
      }
    ]
  }),
  created() {
    this.$store.dispatch("setCompanyList");
    this.$store.dispatch("setDptList", this.memeber);
  },
  computed: {
    dptList: {
      get() {
        return this.$store.getters.getDptList;
      }
    },
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    }
  },
  watch: {
    dptList: function(newVal) {
      this.items = JSON.parse(JSON.stringify(newVal));
    }
  },
  methods: {
    editItem() {
      this.oldTree = this.items;
      this.editable = true;
      this.draggable = true;
      this.droppable = true;
      this.$store.dispatch("setDptList");
      // this.selection.push(item);
    },
    deleteItem() {
      this.editable = false;
    },
    cancelItem() {
      this.draggable = false;
      this.editable = false;
      this.droppable = false;
    },
    deleteSelect(data) {
      var payload = {
        dptId: data.id
      };
      this.$store.dispatch("deleteDept", payload);
    },
    addItem(data) {
      this.editable = true;
      var newItem = {
        name: "신규 부서 명"
      };
      data.children.push(newItem);
    },
    saveItem() {
      this.draggable = false;
      this.droppable = false;
      this.editable = false;
    }
    // drop(node, targetTree, oldTree) {},
    // change(node, targetTree, oldTree) {
    //   // console.log(node);
    //   // console.log(targetTree);
    //   // console.log(oldTree);
    // }
  }
};
</script>

