  display: inline-block;
  min-width: 50px;
  text-align: right;
  margin-right: 5px;
}
.st-department .box-department .btn-area > span em {
  padding-right: 2px;
}
.st-department .box-department .box-area-pdf {
  position: absolute;
  top: 10px;
  left: 44%;
}
.st-department .box-department .box-area-pdf > label {
  width: 100%;
  line-height: 26px;
}
.st-department .box-department .box-area-pdf input {
  width: 200px;
  height: 26px;
  vertical-align: top;
}
.st-department.st-adm .box-department .btn-area {
  left: 88%;
}

.he-tree {
  border: 1px solid #ccc;
  padding: 20px;
}
</style>
