<template>
  <div>
    <v-slide-y-reverse-transition>
      <v-btn
        v-if="bottomMenuDrawer"
        fab
        tile
        fixed
        large
        dark
        style="height:26px; width:88px; bottom: 349px; left: calc(50% + 88px); z-index:4; background-color:#4A367D;"
        class="elevation-0 moove_btn"
        icon
        @click="toggleBottomBtn()"
      >
        <v-icon large>mdi-chevron-down</v-icon>
      </v-btn>
    </v-slide-y-reverse-transition>
    <v-menu
      v-model="bottomMenuDrawer"
      :close-on-content-click="false"
      :close-on-click="false"
      min-width="100%"
      top
      transition="slide-y-reverse-transition"
      z-index="3"
      bottom
    >
      <template v-slot:activator="{ on }">
        <v-slide-y-reverse-transition>
          <v-btn
            v-on="on"
            fab
            tile
            fixed
            large
            dark
            transition="scale-transition"
            style="height:26px; width:88px; bottom: 0px; left: calc(50% + 88px); z-index:2; background-color:#4A367D;"
            class="elevation-0 moove_btn"
            icon
            @click="toggleBottomBtn()"
          >
            <v-icon large>mdi-chevron-up</v-icon>
          </v-btn>
        </v-slide-y-reverse-transition>
      </template>
      <v-card
        color="primary"
        class="pt-2"
        style="left: 256px; width:calc(100vw - 256px); height: 349px; background-color:#4A367D;"
        tile
      >
        <v-tabs
          class="ma-0 elevation-0"
          height="44px"
          hide-slider
          background-color="primary"
        >
          <v-tab class="moove_tab" active-class="tabs__item--active active"
            >전체 차량(차량 수)</v-tab
          >
          <v-tab class="moove_tab" active-class="tabs__item--active active"
            >장기 미운행</v-tab
          >
          <input
            type="text"
            name="carnb"
            id="carnb"
            class="st-input01 mx-5 mt-0"
            style="top:21px; width:200px; color:#000; height:36px; background-color:#fff;"
            placeholder="차량번호 검색"
            v-model="searchCar"
          />
          <v-spacer></v-spacer>
          <v-btn
            style="color:white;"
            class="mx-5"
            width="84"
            height="36"
            color="#177398"
          >
            <v-icon left>mdi-file-excel</v-icon>엑셀
          </v-btn>
          <v-tab-item v-for="n in 2" :key="n" style="background-color: #E1E8F0">
            <v-layout class="pa-3" color="secondary" style="height:297px;">
              <v-flex style="overflow: auto;" color="secondary">
                <v-data-table
                  :headers="headers"
                  :items="rctOpr"
                  item-key="carId"
                  :page.sync="page"
                  :items-per-page="itemsPerPage"
                  :search="searchCar"
                  hide-default-header
                  hide-default-footer
                  no-results-text="검색 차량이 없습니다."
                  :mobile-breakpoint="1"
                  @page-count="pageCount = $event"
                  class="moove_table"
                >
                  <template v-slot:header="{ props: { headers } }">
                    <thead>
                      <tr>
                        <th
                          v-for="header in headers"
                          :key="header.carId"
                          class="moove_table_header"
                        >
                          {{ header.text }}
                        </th>
                      </tr>
                    </thead>
                  </template>
                  <template v-slot:body="{ items }">
                    <tbody>
                      <tr
                        v-for="item in items"
                        :key="item.carNo"
                        @click="setSelectedCar(item)"
                      >
                        <td
                          class="moove_table_td"
                          style="height: 30px;"
                          v-bind:style="getColor(item.engStt)"
                        >
                          {{ item.engStt | engSttWord }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.dptNm ? item.dptNm : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.carNo ? item.carNo : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.dvcId ? item.dvcId : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.lstColTm ? item.lstColTm : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.spd ? item.spd + ' km/h' : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.dayDrvDst ? item.dayDrvDst + ' km/일' : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.dayDrvTm ? item.dayDrvTm : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.dayDrvTm ? item.dayDrvTm : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.fuelEffc ? item.fuelEffc : '-' }}
                        </td>
                        <td class="moove_table_td" style="height: 30px;">
                          {{ item.fuelEffc ? item.fuelEffc : '-' }}
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </v-data-table>
                <div>
                  <v-pagination
                    class="pt-2"
                    v-model="page"
                    :length="pageCount"
                  ></v-pagination>
                </div>
              </v-flex>
            </v-layout>
          </v-tab-item>
        </v-tabs>
      </v-card>
    </v-menu>
  </div>
</template>

<script>
export default {
  name: 'MooveBottomMenu',
  data: () => ({
    searchCar: null,
    headers: [
      {
        text: '상태',
        align: 'center',
        value: 'engStt',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '부서명',
        align: 'center',
        value: 'carNo',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '차량번호',
        align: 'center',
        value: 'carNo',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '회선번호',
        align: 'center',
        value: 'carNo',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '최종수신시간',
        align: 'center',
        value: 'lstColTm',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '현재속도',
        align: 'center',
        value: 'spd',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '일운행거리',
        align: 'center',
        value: 'dayDrvDst',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '일운행시간',
        align: 'center',
        value: 'dayDrvTm',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '연비',
        align: 'center',
        value: 'fuelEffc',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: '운전자명',
        align: 'center',
        value: 'carNo',
        class: 'moove_table_header',
        divider: true
      },
      {
        text: 'etas최근 제출시간',
        align: 'center',
        value: 'lstColTm',
        class: 'moove_table_header',
        divider: true
      }
    ],
    page: 1,
    pageCount: 0,
    itemsPerPage: 6
  }),
  props: {},
  computed: {
    bottomMenuDrawer: {
      get() {
        return this.$store.state.drawer.bottomMenuDrawer;
      },
      set() {}
    },
    rctOpr: {
      get() {
        return this.$store.getters.getRctOprList;
      },
      set() {}
    }
  },
  methods: {
    toggleBottomBtn: function() {
      this.$store.state.drawer.rightNavigationDrawer = false;
      this.$store.state.drawer.bottomMenuDrawer = !this.$store.state.drawer
        .bottomMenuDrawer;
    },
    setSelectedCar: function(rctOpr) {
      if (rctOpr.x === null || rctOpr.y === null) {
        this.$store.state.dialog = true;
        this.$store.state.dialogMessage = '좌표 정보가 없습니다.';
        return;
      }
      this.$store.dispatch('setSelectedRctOprCarNo', rctOpr.carNo);
    },
    getColor: function(status) {
      if (status === 'E') return 'color:#6B52EC';
      else if (status === 'D') return 'color:#2FB53A;';
      else if (status === null) return 'color:#333945';
      else return 'color:#2FB53A';
    }
  },
  filters: {
    engSttWord: function(value) {
      if (!value) return '미수집';
      if (value === 'E') return '꺼짐';
      else if (value === 'D') return '운행중';
      else if (value === null) return '미수집';
      else return '미수집';
    }
  }
};
</script>

<style scoped>
.mytable .v-table tbody tr:not(:last-child) {
  background-color: black !important;
}
.active {
  color: #27166a !important;
  border-bottom: 0;
  border-radius: 5px 5px 0 0;
  background-color: #e1e8ee !important;
}
.moove_tab {
  font-size: 15px;
  font-weight: 600;
  border-radius: 5px 5px 0 0;
  background-color: #7c70a2;
}

.moove_text_field {
  width: 5px !important;
}
.moove_btn {
  border-radius: 5px 5px 0 0;
}
.moove_table {
  background-color: white;
  border: 1px solid #b6b6b6;
  border-radius: 5px;
}
.moove_table_header {
  height: 40px;
  font-size: 13px;
  color: #fff !important;
  border-bottom: 1px solid #cbc7d3;
  background: #6a6180;
  text-align: center !important;
}
.moove_table_td {
  height: 30px;
  text-align: center;
}
</style>
