<template>
  <v-app-bar
    app
    clipped-right
    clipped-left
    class="moove_app_bar"
    color="secondary"
    style="font-size: 22px; font-weight: bolder; color:#000c41; background-color:#E1E8EE"
  >
    VDIS 차량관제서비스
    <v-progress-linear
      :active="getProgress.loading"
      :indeterminate="getProgress.loading"
      absolute
      bottom
      color="deep-purple accent-4"
    ></v-progress-linear>
    <v-spacer></v-spacer>
    <span class="subtitle-2 pt-1">{{memeber.mbrNm ? memeber.mbrNm +"("+memeber.lgnId+") 님" : ""}}</span>
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn v-on="on" icon @click="editMemeber()" class="mr-0 mt-2">
          <v-icon color="primary" style="color:#4A367D;">mdi-account</v-icon>
        </v-btn>
      </template>
      <span>개인정보 수정</span>
    </v-tooltip>

    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn v-on="on" icon @click="clickNotice()" class="mr-3 mt-2">
          <v-badge color="green" overlap>
            <template v-slot:badge>
              <span v-if="notice > 0">{{ notice }}</span>
            </template>
            <v-icon color="primary" style="color:#4A367D;">mdi-bell</v-icon>
          </v-badge>
        </v-btn>
      </template>
      <span>공지사항</span>
    </v-tooltip>
    <v-dialog v-model="dialog" max-width="800">
      <v-card>
        <v-card-title>공지사항</v-card-title>
        <v-divider></v-divider>
        <v-expansion-panels accordion>
          <v-expansion-panel v-for="(notice, i) in notices" :key="i">
            <v-expansion-panel-header>
              <v-row no-gutters>
                <v-col cols="8" v-if="notice.badge !== null">
                  <v-badge color="green">
                    <template v-slot:badge>
                      <span style="font-size : 9px;">{{ notice.badge }}</span>
                    </template>
                    {{ notice.subject }}
                  </v-badge>
                </v-col>
                <v-col v-else cols="8" class="text--secondary">
                  {{
                  notice.subject
                  }}
                </v-col>
                <v-col cols="4" class="text--secondary">
                  {{
                  notice.date
                  }}
                </v-col>
              </v-row>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row no-gutters style="width: 100%">
                <v-col cols="1">작성자</v-col>
                <v-divider vertical class="mx-4"></v-divider>
                <v-col cols="10">{{ notice.writer }}</v-col>
              </v-row>
              <v-row no-gutters style="width: 100%">
                <v-col cols="1">내용</v-col>
                <v-divider vertical class="mx-4"></v-divider>
                <v-col cols="10">{{ notice.content }}</v-col>
              </v-row>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="primary" text @click="dialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-btn color="primary" style="background-color:#4A367D" @click="logout()">
      <span>로그아웃</span>
    </v-btn>
  </v-app-bar>
</template>

<script>
export default {
  name: "Header",
  data: () => ({
    notice: 1,
    dialog: false,
    notices: [
      {
        subject: "관제가 잠시 종료됩니다.",
        date: "2019-11-11",
        writer: "관리자",
        badge: "new",
        content:
          "관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다.관제가 잠시 종료사유는 서버 고장때문입니다."
      },
      {
        subject: "관제 요금이 할인 됩니다.",
        date: "2019-10-11",
        writer: "관리자",
        badge: null,
        content: "1000만원으로 할인됩니다."
      }
    ]
  }),
  computed: {
    getProgress() {
      return this.$store.getters.getProgress;
    },
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    }
  },
  methods: {
    logout: function() {
      // 로그아웃
      localStorage.removeItem("access_token"); // 토큰 삭제 후,
      this.$router.push({ path: "/signin" }); // '/signin' 화면으로 전환
    },
    clickNotice: function() {
      this.dialog = true;
      this.notice = 0;
      this.$store.dispatch("loadingOff");
    },
    loadingTrue: function() {
      this.$store.dispatch("loadingOn");
    },
    editMemeber: function() {
      this.$store.state.dialogMember = true;
    }
  }
};
</script>

<style scoped>
.v-item-group {
  position: static;
}
.moove_app_bar {
  height: 56px;
}
</style>
