<template>
  <div>
    <div class="area-content pa-8">
      <div class="content-tit">
        <h2>차량 정보</h2>
      </div>
      <!-- 내용영역 -->
      <div class="pa-0 area-content">
        <!-- 상단통합검색 -->
        <section class="box-area-search mb25">
          <!-- 부서선택 -->
          <div class="dis-ib st-input-bt mr10">
            <label class="blind" for="car-choice">부서선택</label>
            <input
              type="text"
              name="car-choice"
              id="car-choice"
              class="st-input-02 w140"
              placeholder="부서선택"
            />
            <a
              href="#none"
              class="btn-search"
              onclick="layer_open('layer11'); return false;"
              title="검색"
            >
              <span class="blind">검색</span>
            </a>
          </div>

          <!-- 검색구분 -->
          <div id class="st-select app-select02">
            <v-app>
              <v-select
                v-model="value"
                :items="searchType"
                item-text="name"
                item-value="type"
                label
                outlined
                dense
              ></v-select>
            </v-app>
          </div>

          <!-- 검색내용 -->
          <div class="dis-ib">
            <label class="blind" for="tx-car">검색내용</label>
            <input type="text" name id="tx-foothold2" class="st-input-02 w290" placeholder />
          </div>

          <a href="#none" class="btn-st05 btn-por" style="color:#fff;" title="조회">
            <span dark>조회</span>
          </a>
        </section>
        <!-- //상단통합검색 -->

        <!-- 내용 -->
        <section class="box-area-content">
          <!-- 테이블 -->
          <div id class="st-table06 table01">
            <v-app id>
              <div>
                <v-data-table
                  v-model="selected"
                  :headers="headers"
                  :items="getCarList"
                  :single-select="false"
                  item-key="name02"
                  show-select
                  :page.sync="page"
                  :items-per-page="itemsPerPage"
                  hide-default-footer
                  class="elevation-1"
                  @page-count="pageCount = $event"
                  min-height="560px"
                >
                  <template v-slot:item.action="{ item }">
                    <v-btn
                      small
                      color="primary"
                      style="background-color:#4a367d;"
                      class="ml-2"
                      @click="editCar(item)"
                    >수정</v-btn>
                  </template>
                  <template v-slot:no-data>데이터가 없습니다.</template>
                </v-data-table>
                <div class="text-center pt-2">
                  <v-pagination v-model="page" :length="pageCount"></v-pagination>
                </div>
              </div>
            </v-app>
            <v-dialog v-model="dialog" max-width="1000">
              <v-card>
                <div class="area-content pt-4 px-4">
                  <div class="content-tit">
                    <h2>차량정보 수정</h2>
                  </div>
                </div>
                <div class="st-table-view input-wst1">
                  <table>
                    <caption>차량정보 수정</caption>
                    <colgroup>
                      <col style="width:15%" />
                      <col style="width:35%" />
                      <col style="width:15%" />
                      <col style="width:35%" />
                    </colgroup>
                    <tbody>
                      <tr>
                        <th scope="row" class="compulsory">
                          <label for="tx-name1">차량번호</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name1" class v-model="carModel.carNo" />
                          <a
                            href="#none"
                            class="btn-st05 ml-2"
                            style="color : white;"
                            title="중복확인"
                            @click="duplicateCheckCarNo()"
                          >
                            <span>중복확인</span>
                          </a>
                        </td>
                        <th scope="row" class="compulsory">
                          <label for="tx-name2">회선번호</label>
                        </th>
                        <td>
                          <input
                            type="text"
                            name
                            id="tx-name2"
                            class
                            disabled="disabled"
                            v-model="carModel.dptNm"
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row" class="compulsory">
                          <label for="tx-name3">부서명</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name3" class v-model="carModel.dptNm" />
                          <a
                            href="#none"
                            class="btn-st05 ml-2"
                            style="color : white;"
                            title="조회"
                            @click="innerDialog = !innerDialog"
                          >
                            <span>조회</span>
                          </a>
                        </td>
                        <th scope="row" class="compulsory">
                          <label for="tx-name4">단말기 모델명</label>
                        </th>
                        <td>
                          <input
                            type="text"
                            name
                            id="tx-name4"
                            class
                            v-model="carModel.dvcMdlNm"
                            disabled="disabled"
                          />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row" class="compulsory">
                          <label for="tx-name5">자동차명</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name5" class v-model="carModel.carNm" />
                        </td>
                        <th scope="row" class="compulsory">
                          <label for="tx-name6">운전자명</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name6" class v-model="carModel.drvrNm" />
                          <a
                            href="#none"
                            class="btn-st05 ml-2"
                            title="운전자명 새창열기"
                            style="color : white;"
                            @click="dialog4 = !dialog4"
                          >
                            <span>조회</span>
                          </a>
                        </td>
                      </tr>
                      <tr>
                        <th scope="row" class="compulsory">
                          <label for="tx-name7">분석유형</label>
                        </th>
                        <td>
                          <input
                            type="text"
                            name
                            id="tx-name7"
                            class
                            v-model="carModel.analTp"
                            disabled="disabled"
                          />
                        </td>
                        <th scope="row" class="compulsory">
                          <label for="tx-name8">운전자연락처</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name8" class v-model="carModel.drvrTelNo" />
                        </td>
                      </tr>
                      <tr>
                        <th scope="row" class="compulsory">
                          <label for="tx-name9">연식</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name9" class v-model="carModel.carYer" />
                        </td>
                        <th scope="row" class="compulsory">
                          <label for="tx-name10">사업자등록번호</label>
                        </th>
                        <td>
                          <input type="text" name id="tx-name10" class v-model="carModel.bizNo" />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div style="border-bottom:1px solid #a6a3b5;" class="mx-4 mt-4"></div>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn large color="primary" style="color:#4a367d;" text v-on:click="update">수정</v-btn>
                  <v-btn large color="primary" style="color:#4a367d;" text v-on:click="close">취소</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="innerDialog" max-width="500px" hide-overlay>
              <v-card>
                <div class="area-content pt-4 px-4">
                  <div class="content-tit mb-0">
                    <h2>부서 조회</h2>
                  </div>
                </div>
                <v-container class="py-0 px-2 elevation-0">
                  <v-list class="px-2">
                    <input
                      type="text"
                      name="carnb"
                      style="top:21px; width:100%; height:36px; background-color:#ecf1f7!important;"
                      placeholder="부서 검색"
                      v-model="searchDept"
                    />
                  </v-list>
                  <v-list class="pt-0 pb-0 px-2">
                    <v-card class="elevation-0">
                      <v-layout style="height:220px">
                        <v-flex style="overflow: auto;">
                          <v-card :elevation="0">
                            <v-treeview
                              activatable
                              :items="dptList"
                              :search="searchDept"
                              return-object
                              item-key="id"
                              hoverable
                              dense
                              open-all
                              style="background-color:#f7f8fa!important;"
                            >
                              <template v-slot:prepend="{ item }">
                                <v-icon
                                  v-if="item"
                                  size="16"
                                  v-text="
                          `mdi-${
                            item.id === 1 ? 'folder-network' : 'folder-network'
                          }`
                        "
                                ></v-icon>
                              </template>
                              <template slot="label" style="height: 24px;" slot-scope="{ item }">
                                <div v-on:click="saveDeptFunc(item)">
                                  <a class="pa-0 ma-0" style="font-size:13px;">
                                    {{
                                    item.name
                                    }}
                                  </a>
                                </div>
                              </template>
                            </v-treeview>
                          </v-card>
                        </v-flex>
                      </v-layout>
                    </v-card>
                  </v-list>
                  <v-list class="pt-0 pb-0"></v-list>
                </v-container>
                <v-card-actions></v-card-actions>
              </v-card>
            </v-dialog>
            <div class="box-stbtn">
              <a href="#none" class="btn-st01 btn-icon-delete mr-1" style="color:#fff;" title="삭제">
                <span>삭제</span>
              </a>
              <a
                href="#none"
                class="btn-st01 btn-exceldownload mr-1"
                title="엑셀다운로드"
                style="color:#fff;"
              >
                <span>엑셀 다운로드</span>
              </a>
              <a href="#none" class="btn-st01 btn-exceldownload" style="color:#fff;" title="엑셀업로드">
                <span>엑셀 업로드</span>
              </a>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data: () => ({
    active: [],
    dialog: false,
    innerDialog: false,
    searchDept: null,
    selectedDept: null,
    value: null,
    headers: [
      {
        text: "부서명",
        align: "center",
        sortable: true,
        value: "cmpnNm"
      },
      { text: "차량번호", value: "carNo", align: "center" },
      { text: "회선번호", value: "dvcId", align: "center" },
      { text: "사업자등록번호", value: "bizNo", align: "center" },
      { text: "자동차명", value: "carNm", align: "center" },
      { text: "연식", value: "carYer", align: "center" },
      { text: "분석유형", value: "analTp", align: "center" },
      { text: "운전자명", value: "drvrNm", align: "center", sortable: false },
      {
        text: "운전자연락처",
        value: "drvrTelNo",
        align: "center",
        sortable: false
      },
      {
        text: "수정",
        value: "action",
        align: "center",
        sortable: false
      }
    ],
    page: 1,
    searchType: [
      { name: "차량번호", type: "carNo" },
      { name: "회선번호", type: "wireNo" },
      { name: "운전자명", type: "drvrNm" }
    ],
    pageCount: 0,
    itemsPerPage: 10,
    selected: [],
    carModel: {}
  }),

  created() {
    this.$store.dispatch("setCarList", this.memeber);
  },
  computed: {
    getCarList: {
      get() {
        return this.$store.getters.getCarList;
      }
    },
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    },
    dptList: {
      get() {
        return this.$store.getters.getDptList;
      },
      set() {}
    }
  },
  methods: {
    editCar(car) {
      this.dialog = true;
      this.carModel = Object.assign({}, car);
      // this.$store.dispatch("setDptListByCar", this.carModel);
    },
    deleteItem() {
      this.$store.dispatch("deleteCar", this.selected);
    },
    close() {
      this.dialog = false;
    },
    update() {
      this.$store.dispatch("updateCar", this.carModel);
      this.close();
    },
    duplicateCheckCarNo() {
      this.$store.state.dialog = true;
      this.$store.state.dialogMessage =
        this.carModel.carNo + "는 중복된 차량번호입니다.";
    },
    saveDeptFunc(item) {
      this.carModel.dptId = item.id;
      this.carModel.dptNm = item.name;
      this.innerDialog = false;
    }
  }
};
</script>

<style scoped>
input {
  background-color: #fff !important;
}
</style>
