import Vue from 'vue';
import Router from 'vue-router';
//@은 절대경로
import Signin from '@/layouts/sign/Signin.vue';
import Home from '@/layouts/home/Home.vue';
import NotFount from '@/layouts/error/Error404.vue';

import Map from '@/layouts/home/contents/map/Map.vue';
import DrivingHistory from '@/layouts/home/contents/analysis/DrivingHistory.vue';
import DrivingRoute from '@/layouts/home/contents/analysis/DrivingRoute.vue';
import RestTime from '@/layouts/home/contents/analysis/RestTime.vue';
import RecordPosition from '@/layouts/home/contents/analysis/RecordPosition.vue';
import RecordBook from '@/layouts/home/contents/analysis/RecordBook.vue';
import DriveHabit from '@/layouts/home/contents/analysis/DriveHabit.vue';
import EtasSent from '@/layouts/home/contents/etas-sent/EtasSent.vue';
import EtasSentDownload from '@/layouts/home/contents/etas-sent/EtasSentDownload.vue';
import Car from '@/layouts/home/contents/service-config/Car.vue';
import Company from '@/layouts/home/contents/service-config/Company.vue';
import Department from '@/layouts/home/contents/service-config/Department.vue';
import Driver from '@/layouts/home/contents/service-config/Driver.vue';
import FAQ from '@/layouts/home/contents/service-config/FAQ.vue';
import Notice from '@/layouts/home/contents/service-config/Notice.vue';
import Position from '@/layouts/home/contents/service-config/Position.vue';
import Question from '@/layouts/home/contents/service-config/Question.vue';
import Member from '@/layouts/home/contents/service-config/Member.vue';

Vue.use(Router);

// 토큰이 확인 된 경우, 다음 layout으로 이동
const hasToken = () => (to, from, next) => {
  if (
    localStorage.getItem('access_token') !== null &&
    localStorage.getItem('access_token') !== 'undefined'
  ) {
    return next();
  }
  next('/signin');
};

//url를 통한 이동 시, Router에 걸러져서 html 상, <router-view/>에 component가 들어가짐
export default new Router({
  // 기본은 hash모드 이나, 일반 웹과 같이 사용하기 위해, history 모드로 설정
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      // 메인 화면
      path: '/',
      name: 'home',
      component: Home,
      children: [
        {
          path: '/map',
          name: 'map',
          component: Map,
          meta: {
            hideFooter: true,
            useFullContent: true
          }
        }, //지도 화면
        {
          path: '/driving-history',
          name: 'driving-history',
          component: DrivingHistory
        }, //운행 이력 화면
        {
          path: '/driving-route',
          name: 'driving-route',
          component: DrivingRoute
        }, //운행 경로 화면
        { path: '/rest-time', name: 'rest-time', component: RestTime }, //휴게 시간 화면
        {
          path: '/record-position',
          name: 'record-position',
          component: RecordPosition
        }, //거점 이력 화면
        { path: '/record-book', name: 'record-book', component: RecordBook }, //운행기록부 화면
        { path: '/drive-habit', name: 'drive-habit', component: DriveHabit }, //운전 습관 화면
        { path: '/etas-sent', name: 'etas-sent', component: EtasSent }, //ETAS 제출 화면
        {
          path: '/etas-sent-download',
          name: 'etas-sent-download',
          component: EtasSentDownload
        }, //ETAS 제출 다운로드 화면
        { path: '/company', name: 'company', component: Company }, //회사 관리 화면
        { path: '/department', name: 'department', component: Department }, //부서 관리 화면
        { path: '/driver', name: 'driver', component: Driver }, //운전자 관리 화면
        { path: '/position', name: 'position', component: Position }, //거점 관리 화면
        { path: '/car', name: 'car', component: Car }, //차량 관리 화면
        { path: '/member', name: 'member', component: Member }, //사용자 관리 화면
        { path: '/faq', name: 'faq', component: FAQ }, //FAQ 화면
        { path: '/question', name: 'question', component: Question }, //문의 게시판 화면
        { path: '/notice', name: 'notice', component: Notice } //공지사항 화면
      ],
      // 라우팅 전에 토큰 여부 확인
      beforeEnter: hasToken()
    },
    {
      // '/home'으로 진입 시, '/'로 경로 변경
      path: '/home',
      redirect: '/'
    },
    {
      path: '/signin',
      name: 'signin',
      component: Signin
    },
    {
      // router에 등록되지않은 요청 시, 404페이지 전환
      path: '*',
      name: '404',
      component: NotFount
    }
  ]
});
