<template>
  <div>
    <div class="area-content pa-8">
      <div class="content-tit">
        <h2>고객사 가입정보</h2>
      </div>
      <div class="st-table-view">
        <table>
          <caption>고객사 정보</caption>
          <colgroup>
            <col style="width:12%" />
            <col style="width:38%" />
            <col style="width:12%" />
            <col style="width:38%" />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row" class="compulsory">
                <label for="tx-name1">고객명</label>
              </th>
              <td>
                <input
                  type="text"
                  name
                  id="tx-name1"
                  style="width:300px;"
                  placeholder="넷케이티아이"
                  class="st-input"
                  v-model="cmpnModel.cmpnNm"
                />
              </td>
              <th scope="row" class="compulsory">
                <label for="tx-type1">업종</label>
              </th>

              <td>
                <div id class="st-select st-color-1 app-select03 w200 pl-1">
                  <v-app>
                    <v-select
                      :items="bizTpList"
                      v-model="cmpnModel.bizTp"
                      item-value="bizTp"
                      item-text="name"
                      label
                      outlined
                      dense
                      return-object
                    ></v-select>
                  </v-app>
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row">대표 전화 번호</th>
              <td>
                <div class="box-st-phone">
                  <label for="tx-phone1" class="blind">대표전화번호 첫번째</label>
                  <input
                    type="text"
                    name
                    style="width:300px;"
                    id="tx-phone1"
                    placeholder="032-1234-5678"
                    class="st-input"
                    v-model="cmpnModel.cmpnTelNo"
                    v-mask="cmpnTelNoMask(cmpnModel.cmpnTelNo)"
                    maxlength="14"
                  />
                </div>
              </td>
              <th scope="row" class="compulsory">
                <label for="tx-licenseenb">사업자 번호</label>
              </th>
              <td>
                <input
                  type="text"
                  name
                  style="width:300px;"
                  id="tx-phone1"
                  placeholder="123-45-67890"
                  class="st-input"
                  v-model="cmpnModel.bizNo"
                  v-mask="maskBizNo"
                  maxlength="14"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="compulsory">대표 모바일 번호</th>
              <td>
                <div class="box-st-phone">
                  <label for="tx-smartphone1" class="blind">대표모바일 첫번째</label>
                  <input
                    type="text"
                    name
                    style="width:300px;"
                    id="tx-phone1"
                    class="st-input"
                    v-model="cmpnModel.mngMblNo"
                    v-mask="maskMngMblNo"
                    placeholder="010-1234-5678"
                    maxlength="14"
                  />
                </div>
              </td>
              <th scope="row">대표 팩스 번호</th>
              <td>
                <div class="box-st-phone">
                  <input
                    type="text"
                    name
                    style="width:300px;"
                    id="tx-phone1"
                    class="st-input"
                    placeholder="031-1234-5678"
                    v-model="cmpnModel.faxNo"
                    v-mask="cmpnTelNoMask(cmpnModel.telNo)"
                    maxlength="14"
                  />
                </div>
              </td>
            </tr>
            <!-- <tr>
              <th scope="row" class="compulsory" style="height:50px;">대표 이메일</th>
              <td colspan="3">
                <v-text-field
                  style="width:300px; border-radius: 5px !important;"
                  v-model="cmpnModel.mbrEml"
                  placeholder="abc@gmail.com"
                  :rules="[rules.email]"
                  dense
                  class="st-input mb-3 mt-0"
                ></v-text-field>
              </td>
            </tr>
            <tr>
              <th scope="row">
                <label for="tx-carnb">전체 차량 수</label>
              </th>
              <td>
                <input
                  type="text"
                  name
                  id="tx-carnb"
                  class="ro st-input"
                  value="20"
                  readonly="readonly"
                />
                <div class="gap pa-1">대</div>
              </td>
              <th scope="row">
                <label for="tx-openline1">개통 회선 수</label>
              </th>
              <td>
                <input type="text" name id="tx-openline1" class="ro st-input" readonly="readonly" />
                <div class="gap pa-1">회선</div>
              </td>
            </tr>-->
            <tr>
              <th scope="row" class="txt-top compulsory">주소</th>
              <td>
                <v-row class="pa-0 ma-0">
                  <v-col class="pa-0 mr-2" cols="7" sm="7" md="7" v-show="isSearchAddr">
                    <v-autocomplete
                      :items="addressList"
                      placeholder="경기도 성남시 분당구 운중동"
                      :search-input.sync="searchAddr"
                      :loading="isLoading"
                      hide-no-data
                      item-text="jibunAddr"
                      clearable
                      flat
                      dense
                      hide-details
                      solo-inverted
                      style="width:300px; color:black; font-size:14.4px;"
                      class="ml-1"
                      v-model="select"
                      cache-items
                    >
                      <template v-slot:selection="{ item }">{{ item.jibunAddr }}</template>
                      <template v-slot:item="{ item }">
                        <v-list-item-content @click="selectedPostNo(item.zipNo, item.jibunAddr)">
                          <v-list-item-title v-text="item.jibunAddr"></v-list-item-title>
                          <v-list-item-subtitle v-text="item.zipNo"></v-list-item-subtitle>
                        </v-list-item-content>
                      </template>
                    </v-autocomplete>
                  </v-col>
                  <v-col class="pa-0 ml-1" cols="3" sm="3" md="3">
                    <v-btn
                      @click="isSearchAddr = !isSearchAddr"
                      style="width:200px; background-color:#4a367d;"
                      color="primary"
                    >주소 검색</v-btn>
                  </v-col>
                </v-row>
              </td>
              <th scope="row">
                <label for="tx-openline1">우편번호</label>
              </th>
              <td>
                <input
                  type="text"
                  name
                  id="tx-openline1"
                  class="ro st-input"
                  v-model="cmpnModel.postNo"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="txt-top"></th>
              <td colspan="3" class="pt-0">
                <div class="box-st-add">
                  <label for="tx-add1" class="blind">검색된주소</label>
                  <input
                    type="text"
                    name
                    id="add1"
                    class="mt-1 wp100 st-input"
                    v-model="cmpnModel.addr"
                  />
                  <label for="tx-add2" class="blind">상세주소</label>
                  <input
                    type="text"
                    name
                    id="tx-add2"
                    v-model="cmpnModel.addrDtl"
                    class="mt-2 wp100 st-input"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="compulsory">근무시간</th>
              <td class="pt-0">
                <v-row>
                  <v-col class="pa-1" cols="5" sm="5" md="5">
                    <v-menu
                      ref="menu"
                      v-model="wrkStTmMenu"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      :return-value.sync="cmpnModel.wrkStTm"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="cmpnModel.wrkStTm"
                          label="근무 시작 시간"
                          prepend-icon="mdi-timer"
                          readonly
                          v-on="on"
                          clearable
                          flat
                          dense
                          hide-details
                          solo-inverted
                          style="width:400px; color:black;"
                          class="ml-1"
                        ></v-text-field>
                      </template>
                      <v-time-picker
                        v-if="wrkStTmMenu"
                        v-model="cmpnModel.wrkStTm"
                        full-width
                        @click:minute="$refs.menu.save(cmpnModel.wrkStTm)"
                      ></v-time-picker>
                    </v-menu>
                  </v-col>
                  <v-col class="pt-1" cols="5" sm="5" md="5">
                    <v-menu
                      ref="menu3"
                      v-model="wrkEndTmMenu"
                      :close-on-content-click="false"
                      :nudge-right="40"
                      :return-value.sync="cmpnModel.wrkEndTm"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="290px"
                    >
                      <template v-slot:activator="{ on }">
                        <v-text-field
                          v-model="cmpnModel.wrkEndTm"
                          label="근무 종료 시간"
                          prepend-icon="mdi-timer"
                          readonly
                          v-on="on"
                          clearable
                          flat
                          dense
                          hide-details
                          solo-inverted
                          style="width:400px; color:black;"
                          class="ml-1"
                        ></v-text-field>
                      </template>
                      <v-time-picker
                        v-if="wrkEndTmMenu"
                        v-model="cmpnModel.wrkEndTm"
                        full-width
                        @click:minute="$refs.menu3.save(cmpnModel.wrkEndTm)"
                      ></v-time-picker>
                    </v-menu>
                  </v-col>
                </v-row>
              </td>
              <th scope="row" class="compulsory pt-0 ma-0">
                <label for="tx-day1">장기 미사용 기준일</label>
              </th>
              <td class="pt-0 ma-0">
                <input
                  type="text"
                  name
                  id="tx-day1"
                  class="st-input pt-0 ma-0"
                  v-model="cmpnModel.unusdDay"
                />
                <div class="gap pa-1">일</div>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="mt30 ta_r">
          <v-btn @click="update()" style="background-color:#4a367d;" color="primary">수정</v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mask } from "vue-the-mask";
export default {
  name: "",
  directives: { mask },
  data: () => ({
    isSearchAddr: false,
    wrkStTmMenu: false,
    wrkEndTmMenu: false,
    isLoading: false,
    maskBizNo: "###-##-#####",
    maskTelNo: "0##-####-####",
    maskMngMblNo: "01#-####-####",
    searchType: [
      { name: "차량번호", type: "carNo" },
      { name: "회선번호", type: "wireNo" },
      { name: "운전자명", type: "drvrNm" }
    ],
    cmpnModel: {},
    rules: {
      duplicateBizNo: value => {
        var duplicate = false;
        if (value.length == 10) duplicate = true;
        return duplicate || "이미 등록된 사업자번호입니다.";
      },
      required: value => !!value || "필수 입력 사항입니다.",
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "이메일 형식이 맞지 않습니다.";
      }
    },
    searchAddr: null,
    select: null,
    _timerId: null,
    value: null
  }),

  computed: {
    addressList: {
      get() {
        return this.$store.getters.getAddressList;
      },
      set() {}
    },
    bizTpList: {
      get() {
        return this.$store.getters.getBizTpList;
      },
      set() {}
    },
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    },
    company: {
      get() {
        return this.$store.getters.getCompany;
      }
    }
  },
  watch: {
    searchAddr(val) {
      if (!val) {
        return (this.isLoading = false);
      }
      this.isLoading = true;
      this.clearAddressList();
      this.fetchEntriesDebounced(val);
    },
    company(val) {
      this.cmpnModel = JSON.parse(JSON.stringify(val));
      // this.cmpnModel = {
      //   ...this.cmpnModel,
      //   bizTp: {
      //     name: this.cmpnModel.bizTp,
      //     type: this.cmpnModel.bizTp
      //   }
      // };
      console.log(this.cmpnModel);
    },
    addressList() {
      this.isLoading = false;
    }
  },
  created() {
    this.$store.dispatch("setCompany", this.memeber);
  },
  methods: {
    update() {
      var payload = {
        ...this.cmpnModel,
        bizNo: this.cmpnModel.bizNo.replace(/-/gi, ""),
        cmpnTelNo: this.cmpnModel.cmpnTelNo.replace(/-/gi, ""),
        faxNo: this.cmpnModel.faxNo.replace(/-/gi, ""),
        mngMblNo: this.cmpnModel.mngMblNo.replace(/-/gi, ""),
        wrkEndTm: this.cmpnModel.wrkEndTm.replace(/:/gi, ""),
        wrkStTm: this.cmpnModel.wrkStTm.replace(/:/gi, "")
      };
      this.$store.dispatch("updateCompany", payload);
    },
    selectedPostNo(postNo, jibunAddr) {
      this.cmpnModel.postNo = postNo;
      this.cmpnModel.addr = jibunAddr;
      this.isLoading = false;
    },
    fetchEntriesDebounced(val) {
      clearTimeout(this._timerId);
      this._timerId = setTimeout(() => {
        this.$store.dispatch("setAddress", val);
      }, 800);
    },
    clearAddressList() {
      this.addressList = [];
    },
    cmpnTelNoMask() {
      return "###########";
    }
  }
};
</script>

<style scoped>
.v-subheader {
  color: rgba(74, 54, 125, 0.54) !important;
}
.v-text-field > .v-input__control > .v-input__slot:before {
  border-style: none !important;
}
</style>
`
