<template>
  <v-dialog v-model="innerDialog" max-width="500px" hide-overlay>
    <v-card>
      <div class="area-content pt-4 px-4">
        <div class="content-tit mb-0">
          <h2>비밀 번호 변경</h2>
        </div>
      </div>
      <div class="area-content pt-4 px-4">
        <div class="st-table-view input-wst1">
          <table>
            <caption>사용자정보 추가/수정</caption>
            <colgroup>
              <col style="width:40%" />
              <col style="width:60%" />
            </colgroup>
            <tbody>
              <tr>
                <th scope="row" class="compulsory">
                  <label for="tx-name1">현재 비밀번호</label>
                </th>
                <td>
                  <input type="text" name id="tx-name1" class v-model="memberModel.oldPassword" />
                </td>
              </tr>
              <tr>
                <th scope="row" class="compulsory">
                  <label for="tx-name1">새 비밀번호</label>
                </th>
                <td>
                  <input type="text" name id="tx-name1" class v-model="memberModel.newPassword" />
                </td>
              </tr>
              <tr>
                <th scope="row" class="compulsory">
                  <label for="tx-name1">새 비밀번호 확인</label>
                </th>
                <td>
                  <input
                    type="text"
                    name
                    id="tx-name1"
                    class
                    v-model="memberModel.newPasswordRepeat"
                  />
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn large color="primary" text v-on:click="updatePassword">수정</v-btn>
        <v-btn large color="primary" text v-on:click="closeDialog">취소</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "MooveMemberPassword",
  data: () => ({}),
  props: ["memberModel", "innerDialog"],
  methods: {
    updatePassword() {
      this.innerDialog = false;
      this.$store
        .dispatch("updateMemberPassword", this.memberModel)
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    },
    closeDialog() {
      this.$emit("close-dialog", this.dialog);
    }
  }
};
</script>

