<template>
  <div>
    <!-- 지도 추가-->

    <div id="map" style="height: 100vh;">
      <MooveFloatCardView v-show="this.$store.state.map.toggleMapOverlay" id="overlay" />
      <!-- <MooveFloatCCTVCardView v-show="displayCctv" id="overlayCctv" /> -->
    </div>
    <!-- <div id="map" style="height: 100vh;"></div> -->
  </div>
</template>
<script>
// ol은 'openlayer'약자로 지도 라이브러리 이다
// package.json에 dependency를 추가하여
// 아래와 모든 import는 MooveMap.vue Scope 안에서만 사용 할수 있다.
import Map from "ol/Map.js";
import { Cluster, OSM, Vector as VectorSource } from "ol/source";
import View from "ol/View.js";
import { click } from "ol/events/condition";
import ScaleLine from "ol/control/ScaleLine";
// import FullScreen from 'ol/control/FullScreen';
// import OverviewMap from 'ol/control/OverviewMap';
// import ZoomSlider from 'ol/control/ZoomSlider';
import GeoJSON from "ol/format/GeoJSON";
import XYZ from "ol/source/XYZ";
import MultiPoint from "ol/geom/MultiPoint";
import Overlay from "ol/Overlay";
import Feature from "ol/Feature";
import Point from "ol/geom/Point";
import Select from "ol/interaction/Select";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import {
  Circle as CircleStyle,
  Fill,
  Stroke,
  Icon,
  Style,
  Text
} from "ol/style";
// ol의 css를 import 하여 사용
import "ol/ol.css";
// Smoothing lines using Chaikins algorithm
import smooth from "chaikin-smooth";

import MooveFloatCardView from "@/components/MooveFloatCardView.vue";
// import MooveFloatCCTVCardView from '@/components/MooveFloatCCTVCardView.vue';
export default {
  name: "MooveMap",
  data: () => ({
    dialog: false,
    display: false,
    displayCctv: false,
    dialogMessage: "",
    rctOprList: [], // 자동차 데이터
    clusters: {}, // Openlayer 클러스터  객체
    olMap: {}, // Openlayer 맵 객체
    olView: {}, // Openlayer 뷰 객체
    osmSource: {},
    overlay: {},
    overlayCctv: {},
    polling: null,
    intervalTime: 5000 //차량데이터 요청 인터벌 시간(10초)
  }),
  components: {
    MooveFloatCardView: MooveFloatCardView
    // MooveFloatCCTVCardView: MooveFloatCCTVCardView
  },
  created() {
    this.$store.dispatch("setRctOprList");
  },
  mounted() {
    // MapComponent를 Mounted 할때 1회 실행
    this.getRctListInterval();
    this.setMap();
    this.setLineString();
    this.setOverLay();
    this.setMapEvent();
    this.$store.dispatch("setRctOprList");
  },
  beforeDestroy() {
    clearInterval(this.polling);
  },
  computed: {
    getMapTile() {
      return this.$store.getters.getMapTile;
    },
    getRctOprList() {
      return this.$store.getters.getRctOprList;
    },
    getMember() {
      return this.$store.getters.getMember;
    },
    getSelectedRctOprCarNo() {
      return this.$store.getters.getSelectedRctOprCarNo;
    }
  },
  watch: {
    getMapTile: function(newVal) {
      if (newVal === "geoportail") {
        // 위성사진
        var geoportail = new TileLayer({
          source: new XYZ({
            url:
              "https://wxs.ign.fr/pratique/geoportail/wmts?layer=ORTHOIMAGERY.ORTHOPHOTOS&style=normal&tilematrixset=PM&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpeg&TileMatrix={z}&TileCol={x}}&TileRow={y}"
          })
        });
        this.olMap.getLayers().setAt(1, geoportail);
      }
      if (newVal === "osm") {
        var osm = new TileLayer({ source: this.osmSource });
        this.olMap.getLayers().setAt(1, osm);
      }
    },
    getRctOprList: function(newVal) {
      this.rctOprList = null;
      this.rctOprList = newVal;
      this.setCluster();
      if (this.getSelectedRctOprCarNo === undefined) return;
      this.animateSelectedCar();
    },
    getSelectedRctOprCarNo: function() {
      this.animateSelectedCar();
    }
  },
  methods: {
    setMap: function() {
      // this.vw.ol3.MapOptions = {
      //   basemapType: this.vw.ol3.BasemapType.GRAPHIC,
      //   controlDensity: this.vw.ol3.DensityType.EMPTY,
      //   interactionDensity: this.vw.ol3.DensityType.BASIC,
      //   controlsAutoArrange: true,
      //   homePosition: this.vw.ol3.CameraPosition,
      //   initPosition: this.vw.ol3.CameraPosition
      // };
      // this.vmap = new this.vw.ol3.Map("vmap", this.vw.ol3.MapOptions);
      // Open Street Map
      // https://www.openstreetmap.org/#map=7/35.948/127.736
      this.osmSource = new OSM();
      var osm = new TileLayer({ source: this.osmSource }); // OSM은 OpenStreetMap의 약자로 Api키발급없이 무료로 사용가능
      // // var raster = new TileLayer({
      // //   source: new Stamen({
      // //     layer: 'terrain'
      // //   })
      // // });
      // // MapBox
      // // var key =
      // //   'pk.eyJ1IjoianVuZ2ppaHVuIiwiYSI6ImNrMzVpejcxeTAxdW0zY25zOG5laWRseWIifQ.2g6t35e3pDQGR-WFITF1JA';
      // // var url =
      // //   'https://{a-c}.tiles.mapbox.com/v4/mapbox.world-bright/{z}/{x}/{y}.png?access_token=' +
      // //   key;
      // // var withTransition = new TileLayer({
      // //   source: new XYZ({ url: url })
      // // });
      // // // 위성사진
      // // var geoportail = new TileLayer({
      // //   source: new XYZ({
      // //     url:
      // //       'https://wxs.ign.fr/pratique/geoportail/wmts?layer=ORTHOIMAGERY.ORTHOPHOTOS&style=normal&tilematrixset=PM&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fjpeg&TileMatrix={z}&TileCol={x}}&TileRow={y}'
      // //   })
      // // });
      // //미니맵에 지도 화면
      // // var olOverviewView = new View({ projection: 'EPSG:4326' }); // 미니맵에 넣을 지도
      // //컨트롤 옵션
      var scaleLine = new ScaleLine(); // 맵 좌하단 스케일 기능
      // // var fullScreen = new FullScreen(); // 맵 우상단 fullscreen 기능
      // // var zoomSlider = new ZoomSlider(); // 맵 좌상단 scroll 기능
      // //지도 위에 보여질 화면
      this.olView = new View({
        center: [127.055524, 36.547409], // 한국 위치
        zoom: 8, // 한국 위치
        minZoom: 8,
        projection: "EPSG:4326" // 좌표계 기본값은 'EPSG:3857'이나 'EPSG:4326(WGS84)'로 바꿈
      });
      this.olMap = new Map({
        // 맵이 먼저 그려짐 (mounted안에 olMap이 그려짐)
        layers: [osm],
        target: "map",
        view: this.olView,
        controls: [scaleLine]
      });
    },
    setCluster: function() {
      // 차량 위치 정보를 요청
      // 통신 처리가 비동기로 동작하므로,
      // 1. 맵이 먼저 그려짐 (mounted안에 olMap이 그려짐)
      // 2. 통신 처리 완료 후, response 데이터를 cars에 넣음
      // 3. 클러스터에 넣을 feature를 생성하여 클러스터 객체를 만듬
      // 4. 클러스터 객체를 맵 객체에 추가
      // https://openlayers.org/en/latest/examples/cluster.html?q=cluster

      if (this.rctOprList !== null) {
        console.log(this.rctOprList);
        var features = new Array();
        this.rctOprList.forEach(element => {
          if (element.x !== null && element.y !== null) {
            var count = 0;
            var coordinates = [parseFloat(element.x), parseFloat(element.y)];
            // var coordinates = [
            //   parseFloat(element.x) + Math.random(),
            //   parseFloat(element.y) + Math.random()
            // ];
            var feature = new Feature({
              geometry: new Point(coordinates),
              properties: {
                carNo: element.carNo,
                engstt: element.engStt
              }
            });
            features[count] = feature;
            count++;
          }
        });
        var source = new VectorSource({
          features: features
        });
        var clusterSource = new Cluster({
          distance: parseInt(80, 10),
          source: source
        });
        var styleCache = {};
        var clusters = new VectorLayer({
          source: clusterSource,
          style: function(feature) {
            var size = feature.get("features").length;
            var style = styleCache[size];
            if (size > 1) {
              style = new Style({
                image: new CircleStyle({
                  radius: 35,
                  stroke: new Stroke({
                    color: "#fff"
                  }),
                  fill: new Fill({
                    color: "rgba(33, 150, 243, 0.6)"
                  })
                }),
                text: new Text({
                  text: size.toString(),
                  fill: new Fill({
                    color: "#fff"
                  })
                })
              });
              styleCache[size] = style;
            } else {
              var engstt = feature.get("features")[0].getProperties().properties
                .engstt;
              if (engstt === "D") {
                style = new Style({
                  image: new Icon({
                    anchor: [0.5, 62],
                    anchorXUnits: "fraction",
                    anchorYUnits: "pixels",
                    src: require("@/assets/images/icon-car-01.png")
                  })
                });
              } else if (engstt === "E") {
                style = new Style({
                  image: new Icon({
                    anchor: [0.5, 62],
                    anchorXUnits: "fraction",
                    anchorYUnits: "pixels",
                    src: require("@/assets/images/icon-car-02.png")
                  })
                });
              }
            }
            return style;
          }
        });
      }
      this.olMap.getLayers().setAt(2, clusters);
    },
    animateSelectedCar: function() {
      var _this = this;
      if (_this.rctOprList === null) return;
      _this.rctOprList.forEach(element => {
        if (
          element.x === null ||
          element.y === null ||
          _this.getSelectedRctOprCarNo === null
        )
          return true;
        if (_this.getSelectedRctOprCarNo !== element.carNo) return true;
        _this.$store.dispatch("setSelectedRctOpr", element);
        var center = [element.x, element.y];
        _this.olView.animate(
          {
            center: center,
            duration: 700, // animation 속도
            zoom: _this.olMap.getView().getZoom()
          },
          function() {
            _this.overlay.setPosition(center);
            _this.$store.state.map.toggleMapOverlay = true;
          }
        );
      });
    },
    setLineString: function() {
      var styles = [
        new Style({
          stroke: new Stroke({
            color: "blue",
            width: 3
          })
        }),
        new Style({
          image: new CircleStyle({
            radius: 5,
            fill: new Fill({
              color: "orange"
            })
          }),
          geometry: function(feature) {
            var coordinates = feature.getGeometry().getCoordinates()[0];
            return new MultiPoint(coordinates);
          }
        })
      ];
      var geojsonObject = {
        type: "FeatureCollection",
        crs: {
          type: "name",
          properties: {
            name: "EPSG:4326"
          }
        },
        features: [
          {
            type: "Feature",
            geometry: {
              type: "LineString",
              coordinates: [
                [127.100463, 37.397624],
                [127.104063, 37.28968],
                [127.166224, 37.284681],
                [127.258922, 37.245972],
                [127.758463, 37.249775],
                [128.369644, 37.559367],
                [128.651872, 37.680511],
                [128.772657, 37.669541],
                [128.820937, 37.771537],
                [128.845052, 37.731036]
              ]
            }
          }
        ]
      };
      var features = new GeoJSON().readFeatures(geojsonObject);
      var geometry = features[0].getGeometry();
      var coords = geometry.getCoordinates();
      // Smoothing lines using Chaikins algorithm
      // https://openlayers.org/en/latest/examples/chaikin.html
      var smoothCoords = this.makeSmooth(coords, parseInt(4, 10) || 5);
      geometry.setCoordinates(smoothCoords);
      // var source = new VectorSource({
      //   features: features
      // });
      var layer = new VectorLayer({
        source: null,
        // soure: source
        style: styles
      });
      this.olMap.addLayer(layer);
    },
    setOverLay: function() {
      var overlayElement = document.getElementById("overlay");
      // var overlayElement = document.getElementById('overlayCctv');
      this.overlay = new Overlay({
        position: [127.6917071, 36.5420852],
        element: overlayElement
      });
      this.olMap.addOverlay(this.overlay);
    },
    setMapEvent: function() {
      var _this = this;
      // this.olMap.on('moveend', function(e) {
      //   var payload = [];
      //   payload = e.frameState.extent;
      //   _this.$store.dispatch('setMapBounds', payload);
      // });
      var selectPointerClick = new Select({
        condition: click
      });
      this.olMap.addInteraction(selectPointerClick);
      selectPointerClick.on("select", function(e) {
        _this.$store.state.map.toggleMapOverlay = false;
        // _this.displayCctv = false;
        var feature = e.selected[0];
        if (feature.getGeometry === undefined) return;
        if (feature.getProperties().features === undefined) return;
        var center = [
          e.selected[0].getGeometry().getCoordinates()[0],
          e.selected[0].getGeometry().getCoordinates()[1]
        ];
        _this.olView.animate(
          {
            center: center,
            duration: 700, // animation 속도
            zoom: _this.olMap.getView().getZoom() + 2 // animation zoom level
          },
          function() {
            if (feature.getProperties().features === undefined) return;
            if (feature.getProperties().features.length !== 1) return;
            var selectedCarNo = feature.getProperties().features[0].values_
              .properties.carNo;
            _this.$store.dispatch("setSelectedRctOprCarNo", selectedCarNo);
            if (_this.$store.state.map.toggleMapOverlay === false)
              _this.$store.state.map.toggleMapOverlay = true;
            // _this.overlayCctv.setPosition(e.mapBrowserEvent.coordinate);
            // _this.displayCctv = true;
          }
        );
      });
    },
    makeSmooth: function(path, numIterations) {
      numIterations = Math.min(Math.max(numIterations, 1), 10);
      while (numIterations > 0) {
        path = smooth(path);
        numIterations--;
      }
      return path;
    },
    getRctListInterval: function() {
      this.polling = setInterval(() => {
        this.$store.dispatch("loadingOn");
        this.$store.dispatch("setRctOprList", this.getMember.bizNo);
      }, this.intervalTime);
    }
  }
};
</script>

<style scoped>
#map {
  width: 100%;
  background-color: lightgray;
}
</style>
