// webpack 설정으로 인해 main.js가 가장 먼저 실행 됨. entry(Spring에서 web.xml, android에서 manifest.xml 같은 부분)
// webpack.config.js
// ...
// module.exports = {
//   entry: {
//     main: './src/main.js',
//   }
// }
// ...

// /dist/index.html
// ...
// <div id=app></div>
// <script src=/js/chunk-vendors.f1fd7174.js> </script>
// <script src=/js/app.06ef6358.js> </script>
// ...
//
// chunk-vendors.f1fd7174.js는 node_module 라이브러리 압축파일
// app.06ef6358.js을 열어보면 main.js의 선언부가 '({router:vt,store:$t,vuetify:St,render:function(e){return e(xt)}}).$mount("#app")}' 식으로 되어 있음

import Vue from 'vue'; // VueJS 추가
import App from './App.vue'; // 실행 될 앱 추가
import axios from 'axios'; // 통신 모듈 추가: Axios
import router from './router'; //라우터 모듈 추가 : 가짜 라우팅 기능
import store from './store'; // 전역 저장소 추가

import 'babel-polyfill'; // ie11 지원 저장소 추가
// vuetify라는 UI library 임시 사용 중
// 따라서, html 태그 중 '<v-' 로 시작하는 태그가 있을 시, 이해 하려하지 않아도 됨.
// 예) <v-app> <v-content> 등등
// https://vuetifyjs.com/ko/
import vuetify from './plugins/vuetify'; // 임시 스타일 설정 향후 삭제
import VueTheMask from 'vue-the-mask';
// import('./assets/css/basic_m.css');
import '@/assets/css/basic_m.css'; // 디자인 추가
import '@/assets/css/common.css'; //  디자인 추가
import '@/assets/css/datepicker_ko.css'; //  디자인 추가
import '@/assets/css/layout_m.css'; //  디자인 추가
import '@/assets/css/moove.css';

import 'vuetify/dist/vuetify.min.css'; // 임시 스타일 설정 향후 삭제

Vue.prototype.$Axios = axios; // 통신 모듈 추가: Axios
Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router, // URL로 경로 변경
  store, // 전역 저장소
  vuetify, // 임시 스타일 설정 향후 삭제
  VueTheMask,
  beforeCreate() {
    if (localStorage.getItem('access_token') !== null) {
      this.$store.dispatch('setMember');
    }
  },
  render: function(createElement) {
    return createElement(App);
  }
});
