<template>
  <div>
    <v-card tile class="elevation-0">
      <v-toolbar dark color="primary">
        <v-toolbar-title>문의 게시판</v-toolbar-title>
      </v-toolbar>
      <div class="pa-8 area-content">
        <!-- 상단통합검색 -->
        <section class="box-area-search mb25">
          <!-- 부서선택 -->
          <div class="dis-ib st-input-bt mr10">
            <label class="blind" for="car-choice">부서선택</label>
            <input
              type="text"
              name="car-choice"
              id="car-choice"
              class="st-input-02 w140"
              placeholder="부서선택"
            />
            <a
              href="#none"
              class="btn-search"
              onclick="layer_open('layer11'); return false;"
              title="검색"
            >
              <span class="blind">검색</span>
            </a>
          </div>

          <!-- 검색내용 -->
          <div class="dis-ib">
            <label class="blind" for="tx-car">검색내용</label>
            <input type="text" name id="tx-foothold2" class="ml05 st-input-02 w290" placeholder />
          </div>
          <v-btn color="primary" class="ml-2">조회</v-btn>
        </section>
        <!-- //상단통합검색 -->

        <!-- 내용 -->

        <section class="box-area-content">
          <!-- 테이블 -->
          <div id class="st-table06 table01">
            <div>
              <v-data-table
                v-model="selected"
                :headers="headers"
                :items="getCompanyList"
                item-key="bizNo"
                show-select
                :page.sync="page"
                :items-per-page="itemsPerPage"
                hide-default-footer
                class="elevation-1"
                @page-count="pageCount = $event"
                min-height="560px"
              >
                <template v-slot:item.action="{ item }">
                  <v-btn small color="primary" class="ml-2" @click="editItem(item)">수정</v-btn>
                </template>
              </v-data-table>
              <div class="text-center pt-2">
                <v-pagination v-model="page" :length="pageCount"></v-pagination>
              </div>
            </div>
            <div class="box-stbtn">
              <v-btn color="primary" class="ml-2" @click="editItem()">수정</v-btn>
              <v-btn color="primary" class="ml-2" @click="deleteItem()">삭제</v-btn>
              <v-btn color="primary" class="ml-2" @click="downloadExcel()">엑셀다운로드</v-btn>
              <v-btn color="primary" class="ml-2" @click="uploadExcel()">엑셀업로드</v-btn>
            </div>
          </div>
          <!-- // 휴게시간관리 테이블 -->
        </section>
        <!-- //내용 -->
      </div>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "",
  data: () => ({
    headers: [
      {
        text: "회사명",
        align: "center",
        sortable: true,
        value: "cmpnNm"
      },
      { text: "업종", value: "bizTp" },
      { text: "사업자번호", value: "bizNo" },
      { text: "회사전화번호", value: "cmpnTelNo" },
      { text: "담당자번호", value: "mngMblNo" },
      { text: "우편번호", value: "postNo" },
      { text: "수정 / 삭제", value: "action", sortable: false }
    ],
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    selected: []
  }),

  created() {
    this.$store.dispatch("setCompanyList");
  },
  computed: {
    getCompanyList: {
      get() {
        return this.$store.getters.getCompanyList;
      }
    }
  },
  methods: {}
};
</script>

