<template>
  <div>
    <v-list dark>
      <template v-for="item in menu">
        <v-layout
          v-if="item.heading"
          :key="item.heading"
          row
          align-center
        ></v-layout>
        <v-list-group
          v-else-if="item.children"
          :key="item.name"
          v-model="item.model"
          :prepend-icon="item.model ? item.icon : item['icon-alt']"
          append-icon
        >
          <template v-slot:activator>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>{{ item.name }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
          <v-list-item
            v-for="(child, i) in item.children"
            :key="i"
            v-on:click="menuClick(child.menu_url)"
            style="background-color: #4a367e; height: 54px;"
          >
            <v-list-item-action v-if="child.icon">
              <v-icon>{{ child.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title style="font-size: 16px;">{{
                child.name
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
        <v-list-item
          v-else
          :key="item.name"
          v-on:click="menuClick(child.menu_url)"
        >
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </template>
    </v-list>
  </div>
</template>

<script>
export default {
  name: 'MooveMenu',
  data: () => ({}),
  computed: {
    menu() {
      return this.$store.getters.getMenu;
    }
  },
  created() {
    var default_menu = this.menu.filter(function(menu) {
      return menu.default_yn === 'y';
    });
    this.$router.push({ path: default_menu[0].menu_url }).catch(() => {}); // 찾은 초기 메뉴의 'url'로 화면 전환
  },
  methods: {
    menuClick: function(menu_url) {
      const path = menu_url;
      if (this.$route.path !== path) this.$router.push({ path: menu_url }); // 서버의 무응답으로 메뉴를 못가져올 시, '/map'으로 화면 전환
    }
  }
};
</script>

<style scoped>
.v-list-item__title {
  font-size: 16px;
}
.theme--light.v-navigation-drawer .v-divider {
  border-color: #473483;
}
.v-application--is-ltr .v-list-item__action:first-child,
.v-application--is-ltr .v-list-item__icon:first-child {
  margin-right: 16px !important;
}
</style>
