<template>
  <div style="width: 100px;
    height: 100px; top:-350px; left: -323px; position:absolute;">
    <div class="popup-car">
      <div class="pop-header">
        <span
          class="st-box on"
          v-bind:style="getColor(selectedRctOpr.engStt)"
        >{{ selectedRctOpr.engStt | engSttWord }}</span>
        {{ address ? getAddress(address.display_name) : '주소 생성 중' }}
        <a
          class="btn-popclose-top cbtn-car"
          @click="close()"
        >
          <span class="blind">창닫기</span>
        </a>
      </div>
      <div class="pop-content">
        <div class="st-table03">
          <table>
            <colgroup>
              <col style="width:24%;" />
              <col style="width:26%;" />
              <col style="width:24%;" />
              <col style="width:26%;" />
            </colgroup>
            <tbody>
              <tr>
                <th>차량번호</th>
                <td>{{ selectedRctOpr.carNo ? selectedRctOpr.carNo : '-' }}</td>
                <th>회선번호</th>
                <td>{{ selectedRctOpr.dvcId ? selectedRctOpr.dvcId : '-' }}</td>
              </tr>
              <tr>
                <th>일일 운행시각</th>
                <td>{{ selectedRctOpr.dayDrvTm ? selectedRctOpr.dayDrvTm : '-' }}</td>
                <th>단말상태</th>
                <td>
                  {{
                  selectedRctOpr.dvcStt
                  ? deviceSttWord(selectedRctOpr.dvcStt)
                  : '-'
                  }}
                </td>
              </tr>
              <tr>
                <th>일일 운행거리</th>
                <td>
                  {{
                  selectedRctOpr.dayDrvDst
                  ? selectedRctOpr.dayDrvDst + ' km/일'
                  : '-'
                  }}
                </td>
                <th>일일 최초 시동 On</th>
                <td>
                  {{
                  selectedRctOpr.dayLstEngOff
                  ? selectedRctOpr.dayLstEngOff
                  : '-'
                  }}
                </td>
              </tr>
              <tr>
                <th>속도</th>
                <td>{{ selectedRctOpr.spd ? selectedRctOpr.spd + ' km/h' : '-' }}</td>
                <th>단말모델명</th>
                <td>{{ selectedRctOpr.dvcMdlNm ? selectedRctOpr.dvcMdlNm : '-' }}</td>
              </tr>
              <tr>
                <th>운전자명</th>
                <td>{{ selectedRctOpr.drvrNm ? selectedRctOpr.drvrNm : '-' }}</td>
                <th>일일 마지막 시동 Off</th>
                <td>
                  {{
                  selectedRctOpr.dayLstEngOff
                  ? selectedRctOpr.dayLstEngOff
                  : '-'
                  }}
                </td>
              </tr>
              <tr>
                <th>운전자 연락처</th>
                <td>
                  {{
                  selectedRctOpr.drvrMblNo ? selectedRctOpr.drvrMblNo : '-'
                  }}
                </td>
                <th>최종 데이터 연동 시각</th>
                <td>{{ selectedRctOpr.lstColTm ? selectedRctOpr.lstColTm : '-' }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MooveFloatCardView",
  data: () => ({
    time: 0,
    loading: false
  }),
  computed: {
    selectedRctOpr() {
      if (
        this.$store.getters.getSelectedRctOpr.x !== undefined ||
        this.$store.getters.getSelectedRctOpr.y !== undefined
      ) {
        var payload = {
          x: this.$store.getters.getSelectedRctOpr.x,
          y: this.$store.getters.getSelectedRctOpr.y
        };
        this.$store.dispatch("setSelectedRctOprAddress", payload);
      }
      return this.$store.getters.getSelectedRctOpr;
    },
    address() {
      return this.$store.getters.getSelectedRctOprAddress;
    }
    // icon() {
    //   return require("@/assets/icon.svg");
    // }
  },
  methods: {
    reserve() {
      this.loading = true;
      setTimeout(() => (this.loading = false), 2000);
    },
    close() {
      this.$store.state.map.toggleMapOverlay = false;
      this.$store.dispatch("clearSelectedRctOprCarNo");
    },
    getColor: function(status) {
      if (status === "E") return "background:#000000";
      else if (status === "D") return "background:#009400;";
      else if (status === null) return "background:#000000";
      else return "background:#000000";
    },
    getAddress: function(address) {
      var length = address.split(",").length;
      var reverseAddress = address
        .split(",")
        .reverse()
        .slice(2, length)
        .join(" ");
      return reverseAddress;
    },
    deviceSttWord: function(value) {
      if (value === null) return "단말상태 수집중";
      if (value === "00") return "정상";
      else if (value === "11") return "위치추적장치 이상";
      else if (value === "12") return "속도센서 이상";
      else if (value === "13") return "RPM 센서 이상";
      else if (value === "14") return "브레이크 센서 이상";
      else if (value === "15") return "가속도센서 이상";
      else if (value === "21") return "센서 입력 장치 이상";
      else if (value === "22") return "센서 출력 장치 이상";
      else if (value === "31") return "데이터 출력 장치 이상";
      else if (value === "32") return "통신 장치 이상";
      else if (value === "41") return "운행거리 산정 이상";
      else if (value === "99") return "전원공급 이상";
      else return "단말상태 수집중";
    }
  },
  filters: {
    engSttWord: function(value) {
      if (!value) return "ERROR";
      if (value === "E") return "OFF";
      else if (value === "D") return "ON";
      else if (value === null) return "ERROR";
      else return "ERROR";
    }
  }
};
</script>

<style scoped></style>
