<template>
  <div>
    <section class="bg-login">
      <div class="pop-login">
        <div class="login-header">
          <h1>
            <strong>VDIS</strong>
            <em>(Vehicle Driving Information System)</em>
          </h1>
          <div class="dimg">
            차량관제
            <span>서비스</span>
          </div>
        </div>
        <div class="login-content">
          <div class="login-notic">
            <div class="box-notic">
              <h2>공지사항</h2>
              <ul>
                <li>
                  <a href="#none" class="ellipsis">
                    무버 관리자웹 공지사항 샘플텍스트 무버 관리자웹 공지사항
                    샘플텍스트 내용입니다.
                  </a>
                  <span>2019-11-10</span>
                </li>
                <li>
                  <a href="#none" class="ellipsis">무버 관리자웹 공지사항 샘플텍스트 내용입니다.</a>
                  <span>2019-11-10</span>
                </li>
                <li>
                  <a href="#none" class="ellipsis">무버 관리자웹 공지사항 샘플텍스트 내용입니다.</a>
                  <span>2019-11-10</span>
                </li>
                <li>
                  <a href="#none" class="ellipsis">무버 관리자웹 공지사항 샘플텍스트 내용입니다.</a>
                  <span>2019-11-10</span>
                </li>
              </ul>
            </div>
          </div>
          <div class="login-txt">
            <h2>LOGIN</h2>
            <div class="login-space">
              <fieldset title="아이디/패스워드 로그인">
                <v-form>
                  <v-row class="pa-0 ma-0">
                    <v-col class="pa-0 ma-0" cols="12" sm="8" md="8">
                      <v-text-field
                        outlined
                        dense
                        hide-details
                        v-model="id"
                        placeholder="admin"
                        label="아이디"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="pa-0 ma-0">
                    <v-col class="pa-0 mt-3" cols="12" sm="8" md="8">
                      <v-text-field
                        outlined
                        dense
                        hide-details
                        label="패스워드"
                        name="password"
                        v-model="password"
                        type="password"
                        placeholder="1234"
                        @keyup.enter="login"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-btn
                    dark
                    class="elvation-0"
                    color="primary"
                    style="background-color:#4a367d;"
                    type="button"
                    title="로그인"
                    v-on:click="login"
                  >
                    <span>로그인</span>
                  </v-btn>
                </v-form>
              </fieldset>
              <div class="st-checkbox checkbox01">
                <div class="ml-2">
                  <v-checkbox label="아이디 저장" v-model="isSaveId"></v-checkbox>
                </div>
              </div>
            </div>
            <!-- 모바일앱설치/서비스소개 -->
            <div class="login-appservice">
              <ul>
                <li>
                  <a href="#none" title="모바일앱 설치안내">
                    <span>모바일앱 설치안내</span>
                  </a>
                </li>
                <li class="ico">
                  <a href="#none" title="서비스 소개">
                    <span>서비스 소개</span>
                  </a>
                </li>
              </ul>
            </div>
            <!-- 운영센터 안내 -->
            <div class="login-operation">
              <h3>운영센터 안내</h3>
              <ul>
                <li>
                  <span class="line">문의 대표번호 : 1899-8170</span>
                  <span>이메일 접수 : helpdesk@kti.co.kr</span>
                </li>
              </ul>
            </div>
            <!-- 개인정보처리방침 -->
            <div class="login-individual">
              <a href="#none" class="개인정보처리방침">
                <span>개인정보처리방침</span>
              </a>
            </div>
          </div>
        </div>
      </div>
      <section class="login-footer">
