      </section>
    </section>
  </div>
</template>

<script>
export default {
  name: "signin",
  data: () => ({
    id: null,
    password: null,
    isSaveId: false
  }),
  props: {},
  created() {
    this.id = localStorage.getItem("saveId");
    if (this.id !== null) this.isSaveId = true;
    localStorage.removeItem("access_token"); // 토큰 삭제 후,
  },
  methods: {
    login: async function() {
      var payload = {
        lgnId: this.id,
        mbrPwd: this.password
      };
      try {
        await this.$store.dispatch("setLogin", payload);
        this.$router.push({ path: "/home" });

        if (this.isSaveId === true) {
          localStorage.setItem("saveId", this.id);
        } else {
          localStorage.removeItem("saveId");
        }
      } catch (error) {
        this.$store.state.dialog = true;
        this.$store.state.dialogMessage = "잘못된 아이디 또는 비밀번호입니다.";
        this.password = "";
        this.$router.push({ path: "/signin" });
      }
    }
  }
};
</script>

<style scoped>
.v-application ol,
.v-application ul {
  padding-left: 0px;
}
</style>
