              <colgroup>
                <col style="width:15%" />
                <col style="width:35%" />
                <col style="width:15%" />
                <col style="width:35%" />
              </colgroup>
              <tbody>
                <tr>
                  <th scope="row" class="compulsory">
                    <label for="tx-name1">ID</label>
