                  <td>
                    <input type="text" name id="tx-name1" class v-model="memberModel.lgnId" />
                    <a href="#none" class="btn-st05 ml-2" style="color:white;" title="중복확인 새창열기">
                      <span>중복확인</span>
                    </a>
                  </td>
                  <th scope="row">
                    <label for="tx-name2">사용자명</label>
                  </th>
                  <td>
                    <input type="text" name id="tx-name2" class v-model="memberModel.mbrNm" />
                  </td>
                </tr>
                <tr>
                  <th scope="row" class="compulsory">
                    <label for="tx-name3">비밀번호</label>
                  </th>
                  <td>
                    <input
                      type="password"
                      readonly="readonly"
                      name
                      id="tx-name3"
                      class
                      v-model="memberModel.mbrPwd"
                    />
                    <a
                      href="#none"
                      class="btn-st05 ml-2"
                      style="color:white;"
                      title="비밀번호변경"
                      @click="innerDialog = !innerDialog"
                    >
                      <span>비밀번호변경</span>
                    </a>
                  </td>
                  <th scope="row" class="compulsory">연락처</th>
                  <td>
                    <div class="box-st-phone">
                      <label for="tx-phone1" class="blind">대표전화번호 첫번째</label>
                      <input
                        type="text"
                        name
                        id="tx-phone1"
                        class
                        v-model="memberModel.mbrMblNo"
                        maxlength="4"
                      />
                    </div>
                  </td>
                </tr>
                <tr>
                  <th scope="row" class>
                    <!-- <label for="tx-name5">비밀번호 확인</label> -->
                  </th>
                  <td>
                    <!-- <input type="password" name id="tx-name5" class value /> -->
                  </td>
                  <th scope="row" class="compulsory">
                    <label for="tx-name6">관제형태</label>
                  </th>
                  <td>
                    <div id class="st-select st-color-1 app-select03 w200">
                      <v-app>
                        <v-select
                          label
                          outlined
                          dense
                          :items="moniTpList"
                          v-model="memberModel.moniTp"
                          item-value="moniTp"
