                        ></v-select>
                      </v-app>
                    </div>
                  </td>
                </tr>
                <tr>
                  <th scope="row" class="compulsory">이메일</th>
                  <td>
                    <div class="box-st-email">
                      <label for="tx-email01" class="blind">대표이메일 첫번째</label>
                      <input
                        type="text"
                        name
                        id="tx-email01"
                        class="w200"
                        v-model="memberModel.mbrEml"
                      />
                    </div>
                  </td>
                  <th scope="row" class="compulsory">
                    <label for="tx-name7">관제대상</label>
                  </th>
                  <td>
                    <input type="text" name id="tx-name7" class value />
                    <a href="#none" class="btn-st05 ml-2" style="color:white;" title="변경">
                      <span>변경</span>
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div style="border-bottom:1px solid #a6a3b5;" class="mx-4 mt-4"></div>
