    <MooveMemberPassword
      v-bind:memberModel="this.memberModel"
      v-bind:innerDialog="this.innerDialog"
      @close-dialog="closeDialog"
    />
  </div>
</template>

<script>
import MooveMemberPassword from "@/components/MooveMemberPassword.vue";
export default {
  name: "MooveMemberView",
  data: () => ({
    innerDialog: false
  }),
  props: ["memberModel", "dialog"],
  components: {
    MooveMemberPassword: MooveMemberPassword
  },
  computed: {
    moniTpList: {
      get() {
        return this.$store.getters.getMoniTpList;
      },
      set() {}
    }
  },
  methods: {
    close() {
      this.$emit("close-dialog", this.dialog);
      this.innerDialog = false;
    },
    update() {
      this.$store.dispatch("updateMember", this.memberModel);
      this.close();
    },
    updatePassword() {
      this.innerDialog = false;
    },
    closeDialog() {
      this.innerDialog = false;
    }
  }
};
</script>

