<template>
  <div>
    <div class="area-content pa-8">
      <div class="content-tit">
        <h2>사용자 정보</h2>
      </div>
      <!-- 내용영역 -->
      <div class="pa-0 area-content">
        <!-- 상단통합검색 -->
        <section class="box-area-search mb25">
          <!-- 부서선택 -->
          <div class="dis-ib st-input-bt mr10">
            <label class="blind" for="car-choice">부서선택</label>
            <input
              type="text"
              name="car-choice"
              id="car-choice"
              class="st-input-02 w140"
              placeholder="부서선택"
            />
            <a
              href="#none"
              class="btn-search"
              onclick="layer_open('layer11'); return false;"
              title="검색"
            >
              <span class="blind">검색</span>
            </a>
          </div>

          <!-- 검색구분 -->
          <div id class="st-select app-select02">
            <v-app>
              <v-select
                v-model="value"
                :items="searchType"
                item-text="name"
                item-value="type"
                label
                outlined
                dense
              ></v-select>
            </v-app>
          </div>

          <!-- 검색내용 -->
          <div class="dis-ib">
            <label class="blind" for="tx-car">검색내용</label>
            <input type="text" name id="tx-foothold2" class="st-input-02 w290" placeholder />
          </div>

          <a href="#none" class="btn-st05 btn-por" style="color:#fff;" title="조회">
            <span dark>조회</span>
          </a>
        </section>
        <!-- //상단통합검색 -->

        <!-- 내용 -->
        <section class="box-area-content">
          <!-- 테이블 -->
          <div id class="st-table06 table01">
            <v-app id>
              <div>
                <v-data-table
                  v-model="selected"
                  :headers="headers"
                  :items="memeberList"
                  :single-select="false"
                  item-key="name02"
                  show-select
                  :page.sync="page"
                  :items-per-page="itemsPerPage"
                  hide-default-footer
                  class="elevation-1"
                  @page-count="pageCount = $event"
                  min-height="560px"
                >
                  <template v-slot:item.action="{ item }">
                    <v-btn
                      small
                      color="primary"
                      style="background-color:#4a367d;"
                      class="ml-2"
                      @click="editMember(item)"
                    >수정</v-btn>
                  </template>
                  <template v-slot:no-data>데이터가 없습니다.</template>
                </v-data-table>
                <div class="text-center pt-2">
                  <v-pagination v-model="page" :length="pageCount"></v-pagination>
                </div>
              </div>
            </v-app>
            <MooveMemberView
              v-bind:memberModel="this.memberModel"
              v-bind:dialog="this.dialog"
              @close-dialog="closeDialog"
            ></MooveMemberView>

            <div class="box-stbtn">
              <a href="#none" class="btn-st01 btn-icon-delete mr-1" style="color:#fff;" title="삭제">
                <span>삭제</span>
              </a>
              <a
                href="#none"
                class="btn-st01 btn-exceldownload mr-1"
                title="엑셀다운로드"
                style="color:#fff;"
              >
                <span>엑셀 다운로드</span>
              </a>
              <a href="#none" class="btn-st01 btn-exceldownload" style="color:#fff;" title="엑셀업로드">
                <span>엑셀 업로드</span>
              </a>
            </div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import MooveMemberView from "@/components/MooveMemberView.vue";
export default {
  name: "Member",
  data: () => ({
    dialog: false,
    value: null,
    innerDialog: false,
    memberModel: {},
    headers: [
      {
        text: "사용자 아이디",
        align: "center",
        sortable: true,
        value: "lgnId"
      },
      { text: "사용자명", value: "mbrNm", align: "center" },
      { text: "연락처", value: "mbrMblNo", align: "center" },
      { text: "이메일", value: "mbrEml", align: "center" },
      { text: "관제대상", value: "moniTp", align: "center" },
      {
        text: "수정",
        value: "action",
        align: "center",
        sortable: false
      }
    ],
    page: 1,
    searchType: [
      { name: "차량번호", type: "carNo" },
      { name: "회선번호", type: "wireNo" },
      { name: "운전자명", type: "drvrNm" }
    ],
    pageCount: 0,
    itemsPerPage: 10,
    selected: []
  }),
  components: {
    MooveMemberView: MooveMemberView
  },
  created() {
    this.$store.dispatch("setMemberListByBizNo");
  },
  computed: {
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    },
    memeberList: {
      get() {
        return this.$store.getters.getMemberList;
      }
    },
    moniTpList: {
      get() {
        return this.$store.getters.getMoniTpList;
      },
      set() {}
    }
  },
  methods: {
    editMember(member) {
      this.dialog = true;
      this.memberModel = Object.assign({}, member);
    },
    deleteItem() {
      this.$store.dispatch("deleteMember", this.selected);
    },
    closeDialog() {
      this.dialog = false;
    },
    update() {
      this.$store.dispatch("updateMember", this.memberModel);
      this.close();
    },
    updatePassword() {
      this.innerDialog = false;
    },
    closeInner() {
      this.innerDialog = false;
    }
  }
};
</script>

<style scoped>
input {
  background-color: #fff !important;
}
</style>
