<template>
  <v-navigation-drawer
    color="primary"
    v-model="rightNavigationDrawer"
    app
    clipped
    right
    width="320"
    :mobile-break-point="0"
    class="pa-2"
    style="background-color:#4A367D;"
  >
    <v-card color="secondary" style="height: 100%; background-color:#E1E8EE;" tile>
      <v-tabs background-color="primary" hide-slider right grow class="elevation-0">
        <v-tab class="moove_tab" active-class="tabs__item--active active">전체목록</v-tab>
        <v-tab class="moove_tab" active-class="tabs__item--active active"></v-tab>
        <v-tab class="moove_tab" active-class="tabs__item--active active"></v-tab>
        <v-tab-item v-for="n in 1" :key="n"></v-tab-item>
      </v-tabs>
      <v-container class="py-0 px-2 elevation-0">
        <v-list class="pt-2 pb-0">
          <input
            type="text"
            name="carnb"
            id="carnb"
            class="st-input01 ma-0"
            style="top:21px; width:100%; color:#000; height:36px; background-color:#fff;"
            placeholder="부서 검색"
            v-model="searchDept"
          />
        </v-list>
        <v-list class="pt-2 pb-0">
          <v-card class="elevation-0">
            <v-layout style="height:220px">
              <v-flex style="overflow: auto;">
                <v-card :elevation="0">
                  <v-treeview
                    activatable
                    :items="dptList"
                    :search="searchDept"
                    @update:active="selectDept"
                    return-object
                    hoverable
                    item-key="id"
                    :active.sync="active"
                    dense
                    open-all
                  >
                    <template v-slot:prepend="{ item }">
                      <v-icon
                        v-if="item"
                        size="16"
                        v-text="
                          `mdi-${
                            item.id === 1 ? 'folder-network' : 'folder-network'
                          }`
                        "
                      ></v-icon>
                    </template>
                    <template slot="label" style="height: 24px;" slot-scope="{ item }">
                      <a class="pa-0 ma-0" style="font-size:13px;">
                        {{
                        item.name
                        }}
                      </a>
                    </template>
                  </v-treeview>
                </v-card>
              </v-flex>
            </v-layout>
          </v-card>
        </v-list>
        <v-divider class="my-2"></v-divider>
        <v-list class="pt-0 pb-0">
          <v-row class="pt-0 pb-0">
            <v-col class="pt-0 pb-0">
              <input
                type="text"
                name="carnb"
                id="carnb"
                class="st-input01 ma-0"
                style="top:21px; width:100%; color:#000; height:36px; background-color:#fff;"
                placeholder="차량번호 검색"
                v-model="searchCar"
              />
            </v-col>
          </v-row>
        </v-list>
        <v-list class="pt-2 pb-0">
          <v-card class="pa-0 elevation-0">
            <v-data-table
              dense
              :headers="headers"
              :items="rctOprListByDeptId"
              item-key="carId"
              :page.sync="page"
              :items-per-page="itemsPerPage"
              :search="searchCar"
              hide-default-header
              hide-default-footer
              no-results-text="검색 차량이 없습니다."
              @page-count="pageCount = $event"
              class="moove_table elevation-0"
            >
              <!-- <template v-slot:header="{ props: { headers } }">
                <thead style="background-color:red;">
                  <tr>
                    <th
                      v-for="header in headers"
                      :key="header.carId"
                      class="moove_table_header"
                    >{{ header.text }}</th>
                  </tr>
                </thead>
              </template>-->
              <template v-slot:body="{ items }">
                <tbody>
                  <tr v-for="item in items" :key="item.carNo" @click="setSelectedCar(item)">
                    <td
                      class="moove_table_td"
                      style="height: 30px;"
                      v-bind:style="getColor(item.engStt)"
                    >{{ item.engStt | engSttWord }}</td>
                    <td class="moove_table_td" style="height: 30px;">{{ item.carNo }}</td>
                  </tr>
                </tbody>
              </template>
            </v-data-table>
          </v-card>

          <div class="elevation-0 pt-2">
            <v-pagination v-model="page" :length="pageCount"></v-pagination>
          </div>
        </v-list>
      </v-container>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: "MooveRightNavigationDrawer",
  data: () => ({
    active: [],
    tree: [],
    tree2data: [
      { text: "node 1" },
      { text: "node 2" },
      { text: "node 3" },
      { text: "node 4" }
    ],
    depts: [
      {
        id: 9,
        name: "넷케이티아이",
        children: []
      },
      {
        id: 1,
        name: "넷케이티아이",
        children: [
          {
            id: 2,
            name: "경영지원실"
          },
          {
            id: 3,
            name: "연구소"
          },
          {
            id: 4,
            name: "기술지원"
          },
          {
            id: 5,
            name: "연구소"
          },
          {
            id: 6,
            name: "기술지원"
          },
          {
            id: 7,
            name: "연구소"
          },
          {
            id: 8,
            name: "기술지원"
          }
        ]
      }
    ],
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    headers: [
      {
        text: "상태",
        align: "center",
        divider: true,
        value: "status",
        width: "24%"
      },
      {
        text: "차량번호",
        value: "carNo",
        align: "center",
        width: "30%"
      }
    ],

    open: [1],
    searchDept: null,
    searchCar: null
  }),
  computed: {
    rightNavigationDrawer: {
      get() {
        return this.$store.state.drawer.rightNavigationDrawer;
      },
      set() {}
    },
    rctOprListByDeptId: {
      get() {
        return this.$store.getters.getRctOprListByDeptId;
      },
      set() {}
    },
    dptList: {
      get() {
        return this.$store.getters.getDptList;
      },
      set() {}
    },
    memeber: {
      get() {
        return this.$store.getters.getMember;
      }
    }
  },
  created() {
    this.$store.dispatch("setDptList", this.memeber);
    this.$store.dispatch("setRctOprListByDeptId", this.memeber);
  },
  methods: {
    getColor: function(status) {
      if (status === "E") return "color:#6B52EC";
      else if (status === "D") return "color:#2FB53A;";
      else if (status === null) return "color:#333945";
      else return "color:#2FB53A";
    },
    setSelectedCar: function(rctOpr) {
      if (rctOpr.x === null || rctOpr.y === null) {
        this.$store.state.dialog = true;
        this.$store.state.dialogMessage = "좌표 정보가 없습니다.";
        return;
      }
      this.$store.dispatch("setSelectedRctOprCarNo", rctOpr.carNo);
    },
    selectDept: function() {
      if (this.active.length < 1) return;
      var payload = {
        dptId: this.active[0].dptId
      };
      this.$store.dispatch("setRctOprListByDeptId", payload.dptId);
    }
  },
  filters: {
    engSttWord: function(value) {
      if (!value) return "미수집";
      if (value === "E") return "꺼짐";
      else if (value === "D") return "운행중";
      else if (value === null) return "미수집";
      else return "미수집";
    }
  }
};
</script>

<style scoped>
.v-data-table--dense td {
  height: 32px !important;
}
.active {
  color: #27166a !important;
  border-bottom: 0;
  border-radius: 5px 5px 0 0;
  background-color: #e1e8ee;
}
.moove_tab {
  font-size: 15px;
  font-weight: 600;
}

.moove_table_header {
  height: 40px;
  font-size: 13px;
  color: #fff !important;
  border-bottom: 1px solid #cbc7d3;
  background: #6a6180;
  text-align: center !important;
}

.moove_table {
  background-color: white;
  border-radius: 5px;
}

.moove_table_td {
  height: 30px;
  font-size: 13px;
  text-align: center;
}
</style>
